/* --- SCSS Variables --- */
// Color Palette (Import or Define - keep consistent with your existing files)
$primary-color: #00385E;
$secondary-color: #DDEBF3;
$hover-color: #A4C8E1;
$success-color: #198754;
$warning-color: #ffc107;
$danger-color: #dc3545;
$info-color: #0dcaf0;

// Transaction Status Colors
$status-pending: #6c757d;
$status-processing: #0dcaf0;
$status-completed: #198754;
$status-failed: #dc3545;
$status-cancelled: #6c757d;

// Transaction Type Colors
$type-sales: #198754;
$type-purchase: #dc3545;
$type-adjustment: #ffc107;
$type-transfer: #6c757d;
$type-return: #fd7e14;

// Layout & UI Colors
$app-background-color: #f8f9fa;
$container-background-color: #ffffff;
$text-color-primary: #212529;
$text-color-secondary: #495057;
$text-color-muted: #6c757d;
$border-color: #dee2e6;
$card-border-color: #e9ecef;

// Layout & Spacing
$border-radius: 8px;
$border-radius-large: 12px;
$base-padding: 1.5rem;
$grid-gap: 1.5rem;
$base-margin: 1rem;
$base-shadow: 0 5px 15px rgba(0, 0, 0, 0.07);

/* --- Main Content Area --- */
.main-content {
    flex-grow: 1;
    padding: $grid-gap;
    overflow-y: auto;
}

/* --- Transactions Page Container --- */
.transactions-container {
    background-color: $container-background-color;
    border-radius: $border-radius;
    padding: $base-padding;
    border: 1px solid rgba($border-color, 0.5);
}

/* --- Page Header --- */
.page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: $base-margin;
    margin-bottom: $grid-gap;
    padding-bottom: $base-padding;
    border-bottom: 1px solid $border-color;

    .page-title {
        font-size: 1.75rem;
        font-weight: 600;
        color: $text-color-primary;
        display: flex;
        align-items: center;
        gap: 0.5rem;

        .material-symbols-outlined {
            font-size: 2rem;
            color: $primary-color;
        }
    }

    .header-actions {
        display: flex;
        gap: 0.75rem;
        flex-wrap: wrap;
    }
}

/* --- Statistics Cards --- */
.stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: $base-margin;
    margin-bottom: $grid-gap;
}

.stat-card {
    background-color: $container-background-color;
    border: 1px solid $card-border-color;
    border-radius: $border-radius;
    padding: $base-margin;
    text-align: center;
    transition: transform 0.2s ease, box-shadow 0.2s ease;

    &:hover {
        transform: translateY(-2px);
        box-shadow: $base-shadow;
    }

    .stat-icon {
        font-size: 2.5rem;
        margin-bottom: 0.5rem;
        color: $primary-color;
    }

    .stat-number {
        font-size: 1.5rem;
        font-weight: 700;
        color: $text-color-primary;
        margin-bottom: 0.25rem;
    }

    .stat-label {
        font-size: 0.875rem;
        color: $text-color-secondary;
        text-transform: uppercase;
        letter-spacing: 0.5px;
    }

    // Status-specific stat cards
    &.stat-pending {
        border-left: 4px solid $status-pending;
    }
    &.stat-processing {
        border-left: 4px solid $status-processing;
    }
    &.stat-completed {
        border-left: 4px solid $status-completed;
    }
    &.stat-failed {
        border-left: 4px solid $status-failed;
    }
}

/* --- Filters Section --- */
.filters-section {
    background-color: lighten($secondary-color, 3%);
    border: 1px solid $card-border-color;
    border-radius: $border-radius;
    padding: $base-padding;
    margin-bottom: $grid-gap;
    transition: all 0.3s ease;

    &.collapsed {
        padding: 0.75rem $base-padding;
    }

    .filters-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: $base-margin;

        h3 {
            margin: 0;
            color: $text-color-primary;
            font-size: 1.125rem;
            display: flex;
            align-items: center;
            gap: 0.5rem;
        }

        .filters-toggle {
            background: none;
            border: none;
            color: $text-color-secondary;
            cursor: pointer;
            padding: 0.25rem;
            border-radius: $border-radius;
            transition: all 0.2s ease;

            &:hover {
                color: $primary-color;
                background-color: rgba($primary-color, 0.1);
            }
        }
    }

    .filters-content {
        transition: all 0.3s ease;
        overflow: hidden;

        &.collapsed {
            max-height: 0;
            opacity: 0;
            margin: 0;
        }
    }
}

.filters-form {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: $base-margin;
    align-items: flex-end;

    .filter-group {
        display: flex;
        flex-direction: column;

        label {
            font-weight: 500;
            font-size: 0.875rem;
            margin-bottom: 0.5rem;
            color: $text-color-primary;
        }

        input, select {
            padding: 0.5rem 0.75rem;
            border: 1px solid $border-color;
            border-radius: $border-radius;
            background-color: $container-background-color;
            font-size: 0.875rem;
            transition: border-color 0.2s ease;

            &:focus {
                outline: none;
                border-color: $primary-color;
                box-shadow: 0 0 0 2px rgba($primary-color, 0.2);
            }
        }
    }

    .filter-actions {
        display: flex;
        gap: 0.5rem;

        .btn {
            flex-grow: 1;
            min-width: auto;
        }
    }
}

/* --- Transactions Controls --- */
.transactions-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $grid-gap;
    padding: $base-margin;
    background-color: lighten($secondary-color, 5%);
    border-radius: $border-radius;
    flex-wrap: wrap;
    gap: $base-margin;

    .view-options {
        display: flex;
        gap: 0.5rem;
        align-items: center;

        .view-toggle {
            display: flex;
            gap: 0.25rem;
            background-color: $container-background-color;
            border-radius: $border-radius;
            padding: 0.25rem;
            border: 1px solid $border-color;

            .toggle-btn {
                padding: 0.5rem 1rem;
                border: none;
                background: none;
                color: $text-color-secondary;
                border-radius: calc(#{$border-radius} - 2px);
                cursor: pointer;
                transition: all 0.2s ease;
                font-size: 0.875rem;
                display: flex;
                align-items: center;
                gap: 0.5rem;

                &:hover {
                    color: $primary-color;
                }

                &.active {
                    background-color: $primary-color;
                    color: white;
                }
            }
        }
    }

    .bulk-actions {
        display: flex;
        gap: 0.5rem;
        align-items: center;

        .bulk-select {
            display: flex;
            align-items: center;
            gap: 0.5rem;
            font-size: 0.875rem;
            color: $text-color-secondary;
        }
    }
}

/* --- Transactions Table --- */
.transactions-table-container {
    background-color: $container-background-color;
    border: 1px solid $card-border-color;
    border-radius: $border-radius;
    overflow: hidden;
    margin-bottom: $grid-gap;
}

.transactions-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 0.875rem;

    thead {
        background-color: lighten($secondary-color, 3%);

        th {
            padding: 0.75rem 1rem;
            text-align: left;
            font-weight: 600;
            color: $text-color-primary;
            border-bottom: 1px solid $border-color;
            position: relative;

            &.sortable {
                cursor: pointer;
                user-select: none;

                &:hover {
                    background-color: lighten($secondary-color, 1%);
                }

                &::after {
                    content: '↕';
                    position: absolute;
                    right: 0.5rem;
                    opacity: 0.5;
                    font-size: 0.75rem;
                }

                &.sort-asc::after {
                    content: '↑';
                    opacity: 1;
                }

                &.sort-desc::after {
                    content: '↓';
                    opacity: 1;
                }
            }
        }
    }

    tbody {
        tr {
            border-bottom: 1px solid $card-border-color;
            transition: background-color 0.2s ease;

            &:hover {
                background-color: lighten($secondary-color, 6%);
            }

            &.selected {
                background-color: lighten($primary-color, 40%);
            }
        }

        td {
            padding: 0.75rem 1rem;
            vertical-align: middle;
            border-bottom: 1px solid $card-border-color;

            &.text-center {
                text-align: center;
            }

            &.text-right {
                text-align: right;
            }
        }
    }
}

/* --- Status & Type Badges --- */
.status-badge, .type-badge {
    display: inline-flex;
    align-items: center;
    gap: 0.25rem;
    padding: 0.25rem 0.5rem;
    border-radius: $border-radius;
    font-size: 0.75rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.5px;

    .material-symbols-outlined {
        font-size: 0.875rem;
    }
}

// Status Badge Colors
.status-pending {
    background-color: lighten($status-pending, 35%);
    color: darken($status-pending, 10%);
}

.status-processing {
    background-color: lighten($status-processing, 35%);
    color: darken($status-processing, 15%);
}

.status-completed {
    background-color: lighten($status-completed, 35%);
    color: darken($status-completed, 10%);
}

.status-failed {
    background-color: lighten($status-failed, 35%);
    color: darken($status-failed, 10%);
}

.status-cancelled {
    background-color: lighten($status-cancelled, 35%);
    color: darken($status-cancelled, 10%);
}

// Type Badge Colors
.type-sales {
    background-color: lighten($type-sales, 35%);
    color: darken($type-sales, 10%);
}

.type-purchase {
    background-color: lighten($type-purchase, 35%);
    color: darken($type-purchase, 10%);
}

.type-adjustment {
    background-color: lighten($type-adjustment, 35%);
    color: darken($type-adjustment, 15%);
}

.type-transfer {
    background-color: lighten($type-transfer, 35%);
    color: darken($type-transfer, 10%);
}

.type-return {
    background-color: lighten($type-return, 35%);
    color: darken($type-return, 10%);
}

/* --- Reference Info --- */
.reference-info {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;

    strong {
        color: $text-color-primary;
    }

    small {
        color: $text-color-muted;
        font-size: 0.75rem;
    }
}

/* --- Amount Display --- */
.amount-info {
    text-align: right;

    strong {
        color: $text-color-primary;
        font-size: 0.925rem;
    }

    small {
        display: block;
        color: $text-color-muted;
        font-size: 0.75rem;
        margin-top: 0.125rem;
    }
}

/* --- Action Buttons --- */
.action-buttons {
    display: flex;
    gap: 0.25rem;
    justify-content: center;

    .btn-action {
        padding: 0.375rem;
        border: none;
        background: none;
        color: $text-color-secondary;
        border-radius: $border-radius;
        cursor: pointer;
        transition: all 0.2s ease;
        display: flex;
        align-items: center;
        justify-content: center;

        &:hover {
            background-color: lighten($secondary-color, 5%);
            color: $primary-color;
        }

        &.btn-view {
            &:hover {
                color: $info-color;
            }
        }

        &.btn-edit {
            &:hover {
                color: $warning-color;
            }
        }

        &.btn-retry {
            &:hover {
                color: $warning-color;
            }
        }

        &.btn-cancel {
            &:hover {
                color: $danger-color;
            }
        }

        &.btn-sync {
            &:hover {
                color: $success-color;
            }
        }

        .material-symbols-outlined {
            font-size: 1.125rem;
        }
    }
}

/* --- Transaction Details Modal --- */
.transaction-details {
    max-width: 100%;
    padding: 0;

    .details-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: $grid-gap;
        padding-bottom: $base-margin;
        border-bottom: 2px solid $card-border-color;

        h3 {
            margin: 0;
            display: flex;
            align-items: center;
            gap: 0.5rem;
            color: $text-color-primary;
            font-size: 1.25rem;
        }

        .status-badge {
            font-size: 0.875rem;
            padding: 0.375rem 0.75rem;
        }
    }

    .details-grid {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        gap: $grid-gap;
        margin-bottom: $grid-gap;
    }

    .detail-section {
        background: lighten($secondary-color, 8%);
        border: 1px solid $card-border-color;
        border-radius: $border-radius;
        padding: $base-margin;

        h4 {
            margin: 0 0 0.75rem 0;
            color: $text-color-primary;
            font-size: 1rem;
            display: flex;
            align-items: center;
            gap: 0.5rem;

            .material-symbols-outlined {
                font-size: 1.125rem;
                color: $text-color-secondary;
            }
        }

        .info-grid {
            display: grid;
            gap: 0.5rem;

            p {
                margin: 0;
                padding: 0.25rem 0;
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-bottom: 1px solid rgba($border-color, 0.5);

                &:last-child {
                    border-bottom: none;
                }

                strong {
                    color: $text-color-primary;
                    margin-right: 0.5rem;
                    flex-shrink: 0;
                }
            }
        }
    }

    .amount-details {
        background: $container-background-color;
        border: 1px solid $border-color;
        border-radius: $border-radius;
        padding: 0.75rem;

        .amount-row {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0.25rem 0;
            border-bottom: 1px solid $card-border-color;

            &:last-child {
                border-bottom: none;
            }

            &.total {
                border-top: 2px solid $text-color-primary;
                margin-top: 0.5rem;
                padding-top: 0.5rem;
                font-weight: bold;
                font-size: 1.125rem;
            }

            strong {
                color: $success-color;
                font-weight: 600;

                &.total {
                    color: $primary-color;
                }
            }
        }
    }

    .details-actions {
        margin-top: $grid-gap;
        padding-top: $base-margin;
        border-top: 1px solid $card-border-color;
        display: flex;
        gap: 0.75rem;
        flex-wrap: wrap;
        justify-content: flex-end;
    }
}

/* --- Items Table in Details --- */
.table-responsive {
    background: $container-background-color;
    border-radius: $border-radius;
    border: 1px solid $border-color;
    overflow-x: auto;
    margin-bottom: $base-margin;

    .table {
        margin-bottom: 0;
        font-size: 0.875rem;

        th {
            background: lighten($secondary-color, 3%);
            border-bottom: 2px solid $border-color;
            font-weight: 600;
            color: $text-color-primary;
            padding: 0.75rem 0.5rem;
        }

        td {
            vertical-align: middle;
            border-top: 1px solid $card-border-color;
            padding: 0.75rem 0.5rem;
        }

        .table-totals td {
            background: lighten($secondary-color, 5%);
            font-weight: 600;
            border-top: 2px solid $text-color-primary;
        }
    }
}

/* --- SmartBill Integration --- */
.smartbill-info {
    background: $container-background-color;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    padding: 0.75rem;

    p {
        margin: 0 0 0.5rem 0;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .badge {
        display: inline-block;
        padding: 0.25rem 0.5rem;
        font-size: 0.75rem;
        font-weight: 600;
        border-radius: $border-radius;
        text-transform: uppercase;
        letter-spacing: 0.5px;

        &.badge-success {
            background-color: lighten($success-color, 35%);
            color: darken($success-color, 10%);
        }

        &.badge-warning {
            background-color: lighten($warning-color, 25%);
            color: darken($warning-color, 20%);
        }

        &.badge-danger {
            background-color: lighten($danger-color, 35%);
            color: darken($danger-color, 10%);
        }
    }
}

/* --- Audit Trail --- */
.audit-trail {
    max-height: 300px;
    overflow-y: auto;
    background: $container-background-color;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    padding: 0.75rem;

    .audit-entry {
        padding: 0.75rem;
        border-bottom: 1px solid $card-border-color;
        margin-bottom: 0.75rem;

        &:last-child {
            border-bottom: none;
            margin-bottom: 0;
        }

        .audit-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 0.5rem;

            strong {
                color: $text-color-primary;
                text-transform: capitalize;
            }

            .audit-time {
                font-size: 0.75rem;
                color: $text-color-muted;
                font-weight: normal;
            }
        }

        .audit-details {
            color: $text-color-secondary;
            font-size: 0.875rem;
            line-height: 1.4;
        }
    }
}

/* --- Loading & Error States --- */
.loading-state, .error-state {
    text-align: center;
    padding: 2rem;
    color: $text-color-secondary;

    .material-symbols-outlined {
        font-size: 2rem;
        margin-bottom: 0.5rem;
    }
}

.loading-state .material-symbols-outlined {
    color: $primary-color;
    animation: rotate 1s linear infinite;
}

.error-state .material-symbols-outlined {
    color: $danger-color;
}

/* --- Pagination --- */
.pagination-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: $grid-gap;
    padding: $base-margin;
    background-color: lighten($secondary-color, 5%);
    border-radius: $border-radius;

    .pagination-info {
        color: $text-color-secondary;
        font-size: 0.875rem;
    }

    .pagination-controls {
        display: flex;
        gap: 0.5rem;
        align-items: center;

        .page-btn {
            padding: 0.5rem 0.75rem;
            border: 1px solid $border-color;
            background-color: $container-background-color;
            color: $text-color-secondary;
            border-radius: $border-radius;
            cursor: pointer;
            transition: all 0.2s ease;
            text-decoration: none;
            font-size: 0.875rem;

            &:hover:not(:disabled) {
                background-color: $primary-color;
                color: white;
                border-color: $primary-color;
            }

            &.active {
                background-color: $primary-color;
                color: white;
                border-color: $primary-color;
            }

            &:disabled {
                opacity: 0.5;
                cursor: not-allowed;
            }
        }
    }
}

/* --- Animations --- */
@keyframes rotate {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}

@keyframes fadeIn {
    from { 
        opacity: 0;
        transform: translateY(10px);
    }
    to { 
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes slideIn {
    from {
        transform: translateX(-100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

.fade-in {
    animation: fadeIn 0.3s ease-out;
}

.slide-in {
    animation: slideIn 0.3s ease-out;
}

/* --- Responsive Design --- */
@media (max-width: 768px) {
    .page-header {
        flex-direction: column;
        align-items: stretch;
        gap: 0.75rem;

        .header-actions {
            justify-content: center;
        }
    }

    .stats-grid {
        grid-template-columns: repeat(2, 1fr);
    }

    .filters-form {
        grid-template-columns: 1fr;
        gap: 0.75rem;
    }

    .transactions-controls {
        flex-direction: column;
        align-items: stretch;
        gap: 0.75rem;
    }

    .transactions-table-container {
        font-size: 0.75rem;
        overflow-x: auto;
    }

    .transactions-table {
        min-width: 700px;

        th, td {
            padding: 0.5rem 0.25rem;
        }
    }

    .details-grid {
        grid-template-columns: 1fr;
        gap: $base-margin;
    }

    .details-header {
        flex-direction: column;
        align-items: flex-start;
        gap: 0.75rem;
    }

    .details-actions {
        justify-content: center;
        flex-direction: column;
    }

    .amount-row {
        flex-direction: column;
        align-items: flex-start;
        gap: 0.25rem;
    }

    .action-buttons {
        flex-direction: column;
        gap: 0.5rem;

        .btn-action {
            justify-content: flex-start;
            padding: 0.5rem;
        }
    }
}

@media (max-width: 480px) {
    .stats-grid {
        grid-template-columns: 1fr;
    }

    .transactions-table {
        min-width: 600px;
    }

    .pagination-container {
        flex-direction: column;
        gap: 0.75rem;
    }
}

/* --- Print Styles --- */
@media print {
    .transactions-controls,
    .filters-section,
    .details-actions,
    .action-buttons {
        display: none !important;
    }

    .transactions-table-container,
    .transaction-details {
        border: 1px solid #000 !important;
        box-shadow: none !important;
    }

    .transactions-table {
        font-size: 0.75rem;
    }

    .page-header {
        border-bottom: 2px solid #000;
    }
}